import DialogContent from '@mui/material/DialogContent';
import { useState, useEffect } from 'react'
import DialogTitle from '@mui/material/DialogTitle';
import Button from '@mui/material/Button';
import { useDispatch,useSelector } from 'react-redux';
import CustomizedDialog from './CustomizedDialog'
import styless from '../styles/ChangeInformDialog.module.css'
import CloseIcon from '@mui/icons-material/Close';



const ChangeInformDialog = ({ handleClose, handleCloseMenu, cardType, newKandzi, newTranslate, newRead, newImage, idCard, openDialog, closeDialog }) => {
   
    const dispatch = useDispatch()
    const statePosts=useSelector(state=>state.registerInform.posts)
    const [updateInf, setUpdateInf] = useState({
        kandzi: newKandzi,
        read: newRead,
        translate: newTranslate
    })
    const cardTypes = useSelector(state => {
        const state2 = state.registerInform.allLaungages.filter((lng)=>{
return lng.type == cardType
        })
        return state2
    })
    const [cardTypeColon] = cardTypes

useEffect(()=>{
    setUpdateInf(prev => ({
        ...prev,
        kandzi: newKandzi
    }))
    setUpdateInf(prev => ({
        ...prev,
        translate: newTranslate
    }))
    setUpdateInf(prev => ({
        ...prev,
        read: newRead
    }))


},[statePosts])
    return (
        <CustomizedDialog fullWidth={true}
            maxWidth={'sm'}
            open={openDialog}
            onClose={handleClose}
        >
            <div className={styless.closeButton} onClick={handleClose}>
                <CloseIcon style={{ color: "white", cursor: 'pointer' }} />
            </div>

            <DialogContent>
                <div>
                    <div id='formkandzi'>


                       {cardType == 'jp' ||cardType=='cn' || cardTypeColon?.colon == 3  ?  <>  <div className={styless.kandzitext}>

                            <strong></strong>
                        </div>
                        <div className={styless.kandziinput}>
                            <input className={styless.kandzi} defaultValue={newKandzi} type="text" onChange={(e) => {
                                setUpdateInf(prev => ({
                                    ...prev,
                                    kandzi: e.target.value
                                }))
                            }} name="kandzi" />
                        </div></>:null } 
                        <div className={styless.kandzitext}>
                            <strong>{cardType == 'jp' ||cardType=='cn' || cardTypeColon?.colon == 3 ? '' : ''}</strong>
                        </div>
                        <div className={styless.readinput}>
                            <input className={styless.read} type="text" defaultValue={newRead} name="read" onChange={(e) => {
                                setUpdateInf(prev => ({
                                    ...prev,
                                    read: e.target.value
                                }))

                            }} />
                        </div>
                        <div className={styless.kandzitext}>
                            <strong></strong>
                        </div>
                        <div className={styless.translateinput}>
                            <input className={styless.translate} type="text" defaultValue={newTranslate} name="translate" onChange={(e) => {
                                setUpdateInf(prev => ({
                                    ...prev,
                                    translate: e.target.value
                                }))
                            }} />
                        </div>
                        <div className={styless.saveButton}>
                        {cardType == 'jp' ||cardType=='cn' || cardTypeColon?.colon == 3 ? <Button style={{ color: "black", border: " 2px solid white", backgroundColor: "white", borderRadius: 0 }} onClick={() => {
                                if (updateInf.kandzi == '' || updateInf.read == '' || updateInf.translate == '') {
                                    let emptyInput = []
                                       if(cardType == 'jp' ||cardType=='cn' || cardTypeColon?.colon == 3){
                                    if (updateInf.kandzi == '') {
                                        emptyInput.push('  ')

                                    }
                                }
                                    if (updateInf.read == "") {
                                        emptyInput.push('  ')
                                    }
                                    if (updateInf.translate == "") {
                                        emptyInput.push('  ')
                                    }

                                    import("../redux/actions").then(actions => {
                                        dispatch(actions.showMessage(emptyInput.join('; '), 'warning'))
                                    })
                                    setTimeout(() => {
                                        import("../redux/actions").then(actions => {
                                            dispatch(actions.hideMessage())
                                        })
                                    }, 6000)
                                    return
                                }
                                import("../redux/actions").then(actions => {
                                    dispatch(actions.updateParamsOfCardX(updateInf.kandzi, updateInf.read, updateInf.translate, newImage, idCard, cardType))

                                })
                                closeDialog()
                                handleClose()
                                handleCloseMenu()
                            }}> </Button>: <Button style={{ color: "black", border: " 2px solid white", backgroundColor: "white", borderRadius: 0 }} onClick={() => {
                                if (updateInf.read == '' || updateInf.translate == '') {
                                    let emptyInput = []
                                     
                                    if (updateInf.read == "") {
                                        emptyInput.push('  ')
                                    }
                                    if (updateInf.translate == "") {
                                        emptyInput.push('  ')
                                    }

                                    import("../redux/actions").then(actions => {
                                        dispatch(actions.showMessage(emptyInput.join('; '), 'warning'))
                                    })
                                    setTimeout(() => {
                                        import("../redux/actions").then(actions => {
                                            dispatch(actions.hideMessage())
                                        })
                                    }, 6000)
                                    return
                                }
                                import("../redux/actions").then(actions => {
                                    dispatch(actions.updateParamsOfCardX(updateInf.kandzi, updateInf.read, updateInf.translate, newImage, idCard, cardType))

                                })
                                closeDialog()
                                handleClose()
                                handleCloseMenu()
                            }}> </Button> }
                        </div>



                    </div>



                </div>
                {/* <Button style={{ color: "black", border: " 2px solid white", backgroundColor: "white", borderRadius: 0 }} onClick={() => {
                    // dispatch(updateParamsOfCard(newKandzi, newRead, newTranslate, newImage, idCard, cardType))
                    // closeDialog()
                    // handleClose()

                }}> </Button> */}
            </DialogContent>


        </CustomizedDialog>
    )
}
export default ChangeInformDialog