import React from 'react';
import { Route, Routes } from 'react-router-dom'
import RegisterPage from "./components/RegisterPage"
import Cardsjp from "./components/Cardsjp"
import LoginPage from "./components/LoginPage"
import ErrorPage from "./components/ErrorPage"
import { useSelector, useDispatch } from 'react-redux'
import Snackbar from '@mui/material/Snackbar';
import Backdrop from '@mui/material/Backdrop';
import CircularProgress from '@mui/material/CircularProgress';
import MuiAlert from '@mui/material/Alert';
import Header from './components/Header'


const Alert2 = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});




function App() {

  const state = useSelector(state => state.anyMessage)
  const backDropState=useSelector(state=>state.backDrop)
  const dispatch = useDispatch()
  console.log(state)




  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }


  }

  return (
    <div className="App">
      <Header/>
      <br />
      <br />
      <Backdrop
        sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
        open={backDropState != undefined ? backDropState : false}
        
      >
        <CircularProgress color="inherit" />
      </Backdrop>
      <Snackbar open={state != undefined ? state.openMessage : false} autoHideDuration={4000} onClose={() => { 
        import('./redux/actions').then(actions=>{
          dispatch(actions.hideMessage())
        })
       }}>
        <Alert2 onClose={() => {
          import('./redux/actions').then(actions=>{
            dispatch(actions.hideMessage())
          })
          
        }} severity={state != undefined ? state.typeMessage : ""} sx={{ width: '100%' }}>
          {state != undefined ? state.message : ""}
        </Alert2>
      </Snackbar>

      <Routes>
        <Route exact path="/register" element={<RegisterPage />} />
        <Route exact path="/:laungage" element={<Cardsjp />} />
        <Route exact path="/login" element={<LoginPage />} />
        
        <Route exact path="*" element={<ErrorPage />} />



      </Routes>

    </div>
  );
}

export default App;
