export default function TestPage({ cardType, rows, totalCount, setIsTestPage, isTestPage }) {

    const [order, setOrder] = React.useState(DEFAULT_ORDER);
    const [orderBy, setOrderBy] = React.useState(DEFAULT_ORDER_BY);
    const [selected, setSelected] = React.useState([]);
    const [page, setPage] = React.useState(1);
    const [dense, setDense] = React.useState(false);
    const [visibleRows, setVisibleRows] = React.useState(rows);
    const [rowsPerPage, setRowsPerPage] = React.useState(DEFAULT_ROWS_PER_PAGE);
    const [paddingHeight, setPaddingHeight] = React.useState(0);
    const [checkAllClick, setCheckAllClick] = React.useState(0)
    const [clickNextButton, setClickNextButton] = React.useState(0)
    const [pageVisited, setPageVisited] = React.useState([1])
    const [deleteObject, setDeleteObject] = React.useState([])
    const [newTotalCount, setNewTotalCount] = React.useState(totalCount)
    const [testIsStarted, setTestIsStarted] = React.useState(false)
    console.log('newTotalCount', newTotalCount)
    const [buttonIsLoading, setButtonIsLoading] = React.useState({
        buttonPrev: false,
        buttonNext: false
    })
    
    
    

    useEffect(() => {
        setIsTestPage(true);

        return () => {
            setIsTestPage(false);
        };
    }, []);
    useEffect(() => {
        console.log(' ', visibleRows)
        console.log('', deleteObject)
        console.log(checkArrays(deleteObject, visibleRows))
        if (checkAllClick == 1 && !checkArrays(deleteObject, visibleRows) && !(selected.length >= newTotalCount)) {
            handleNextAllClick()
        }

    }, [visibleRows])
    console.log('totalCount', totalCount)
    console.log('', visibleRows.length)


const startTest=()=>{
    setTestIsStarted(true)
}
const finishTest=()=>{
    setTestIsStarted(false)
}

    useEffect(() => {
        dispatch(getCards(JSON.parse(localStorage.getItem('dataForGetCards')).emailOfUser, cardType, page, 1))
    }, [])

    console.log('', rows)

    const dispatch = useDispatch()

    useEffect(() => {

        setVisibleRows(rows)
    }, [rows]);

    React.useEffect(() => {
        let rowsOnMount = stableSort(
            rows,
            getComparator(DEFAULT_ORDER, DEFAULT_ORDER_BY),
        );

        rowsOnMount = rowsOnMount.slice(
            0 * DEFAULT_ROWS_PER_PAGE,
            0 * DEFAULT_ROWS_PER_PAGE + DEFAULT_ROWS_PER_PAGE,
        );

        setVisibleRows(rowsOnMount);
    }, []);

    const handleRequestSort = React.useCallback(
        (event, newOrderBy) => {
            const isAsc = orderBy === newOrderBy && order === 'asc';
            const toggledOrder = isAsc ? 'desc' : 'asc';
            setOrder(toggledOrder);
            setOrderBy(newOrderBy);

            const sortedRows = stableSort(rows, getComparator(toggledOrder, newOrderBy));
            const updatedRows = sortedRows.slice(
                page * rowsPerPage,
                page * rowsPerPage + rowsPerPage,
            );

            setVisibleRows(updatedRows);
        },
        [rows, order, orderBy, page, rowsPerPage],
    );

    function checkArrays(arr1, arr2) {
        let A = []
        arr2.find((e) => {
            A.push(e.idOfCard)
        })
        for (let i = 0; i < arr1.length; i++) {
            if (A.includes(arr1[i])) {
                return true;
            }
        }
        return false;
    }

    const handleSelectAllClick = (event) => {
        setDeleteObject([])
        if (event.target.checked) {

            setCheckAllClick(1)
            setNewTotalCount(totalCount)
            const newSelected = rows.map((n) => n.idOfCard);
            setSelected(newSelected);
            return;
        }
        setNewTotalCount(totalCount)
        setCheckAllClick(0)
        setDeleteObject([])
        setSelected([]);
    };
    const handleNextAllClick = () => {
        const newSelected = rows.map((n) => n.idOfCard);
        setSelected([...selected, ...newSelected]);
        return;
    }

    const offTest=()=>{
        setTestIsStarted(false)
        setSelected([])
        setDeleteObject([])
        setCheckAllClick(0)
    }

    const handleClick = (event, name) => {
        const selectedIndex = selected.indexOf(name);



        let newSelected = [];

        if (selectedIndex === -1) {
            newSelected = newSelected.concat(selected, name);
        } else if (selectedIndex === 0) {
            newSelected = newSelected.concat(selected.slice(1));
        } else if (selectedIndex === selected.length - 1) {
            newSelected = newSelected.concat(selected.slice(0, -1));
        } else if (selectedIndex > 0) {
            newSelected = newSelected.concat(
                selected.slice(0, selectedIndex),
                selected.slice(selectedIndex + 1),
            );
        }

        setSelected(newSelected);


    };






    const isSelected = (name) => selected.indexOf(name) !== -1;
    if (testIsStarted == true) {
        console.log('TESTISSTARTED')
        return <TestStartedPage cardType={cardType} totalCount={totalCount} deleteObject={deleteObject} checkAllClick={checkAllClick} offTest={offTest} selected={selected}/>  
    }
    return (
        <Box sx={{ width: '100%' }}>
            <Paper sx={{ width: '100%', mb: 2 }}>
                <EnhancedTableToolbar startTest={startTest} finishTest={finishTest} checkAllClick={checkAllClick} totalCount={newTotalCount} numSelected={selected.length} />
                <TableContainer>
                    <Table
                        sx={{ minWidth: '100%' }}
                        aria-labelledby="tableTitle"
                        size={dense ? 'small' : 'medium'}

                    >
                        <EnhancedTableHead

                            numSelected={selected.length}
                            order={order}
                            orderBy={orderBy}
                            onSelectAllClick={handleSelectAllClick}
                            onRequestSort={handleRequestSort}
                            rowCount={rows.length}

                        />
                        <TableBody >
                            {visibleRows[0]
                                ? visibleRows.map((row, index) => {
                                    const isItemSelected = isSelected(row.idOfCard);
                                    const labelId = `enhanced-table-checkbox-${index}`;

                                    return (
                                        <TableRow
                                            hover
                                            onClick={(event) => {
                                                handleClick(event, row.idOfCard)

                                                if (checkAllClick == 1) {
                                                    if (isItemSelected) {
                                                        setNewTotalCount((prev) => prev - 1)
                                                        setDeleteObject([...deleteObject, row.idOfCard])
                                                        console.log('', deleteObject)

                                                    }
                                                    else {
                                                        const deleteObjectNew = deleteObject.filter((e) => {
                                                            return e != row.idOfCard
                                                        })
                                                        setDeleteObject(deleteObjectNew)
                                                        setNewTotalCount((prev) => prev + 1)
                                                    }

                                                }
                                            }}
                                            role="checkbox"
                                            aria-checked={isItemSelected}
                                            tabIndex={-1}
                                            key={row.translate}
                                            selected={isItemSelected}
                                            sx={{ cursor: 'pointer' }}
                                        >

                                            <TableCell padding="checkbox">

                                                <Checkbox
                                                    color="primary"
                                                    checked={isItemSelected}
                                                    inputProps={{
                                                        'aria-labelledby': labelId,
                                                    }}

                                                />

                                            </TableCell>
                                            <TableCell
                                                component="th"
                                                id={labelId}
                                                scope="row"
                                                padding="none"


                                            >
                                                {row.translate}
                                            </TableCell>
                                            {getLanguageFromUrl(window.location.href)=='cn' || getLanguageFromUrl(window.location.href)=='jp' || localStorage.getItem('colon')==='3'  ? <TableCell align="right">{row.kandzi}</TableCell> : null} 
                                            <TableCell align="right">{row.read}</TableCell>


                                        </TableRow>
                                    );
                                })
                                : <div style={{ height: '200px' }}><CircularProgress sx={{ position: 'absolute', top: '50%', left: '50%', transform: 'translate(-50%, -50%)' }} disableScrollLock={true} color="inherit" /></div>}
                            {paddingHeight > 0 && (
                                <TableRow
                                    style={{
                                        height: paddingHeight,
                                    }}
                                >
                                    <TableCell colSpan={15} />
                                </TableRow>
                            )}
                        </TableBody>
                    </Table>
                </TableContainer>
                <div style={{ display: 'flex', gap: '20px', justifyContent: 'flex-end', padding: '20px' }}>
                    <Button variant='outlined' sx={{ display: buttonIsLoading.buttonPrev == true ? 'block' : "none" }} disabled>...</Button>
                    <Button sx={{ display: buttonIsLoading.buttonPrev == true ? 'none' : "block" }} variant="outlined" disabled={page == 1 ? true : false} onClick={() => {
                        setClickNextButton(0)
                        setButtonIsLoading(prev => ({
                            ...prev,
                            buttonPrev: true
                        }))
                        setTimeout(() => {
                            setButtonIsLoading(prev => ({
                                ...prev,
                                buttonPrev: false
                            }))
                        }, 700)
                        dispatch(getCards(JSON.parse(localStorage.getItem('dataForGetCards')).emailOfUser, 'jp', page - 1, 1))
                        setPage(page - 1)
                        setPageVisited([...pageVisited, page - 1])
                    }}>

                        .
                    </Button>
                    <Button variant='outlined' sx={{ display: buttonIsLoading.buttonNext == true ? 'block' : "none" }} disabled>...</Button>

                    <Button variant="outlined" sx={{ display: buttonIsLoading.buttonNext == true ? 'none' : "block" }} disabled={visibleRows.length < 15 || visibleRows.length == 0 || page === Math.ceil(totalCount / 15) ? true : false} onClick={() => {
                        setClickNextButton(1)
                        setButtonIsLoading(prev => ({
                            ...prev,
                            buttonNext: true
                        }))
                        setTimeout(() => {



                            setButtonIsLoading(prev => ({
                                ...prev,
                                buttonNext: false
                            }))
                        }, 700)

                        dispatch(getCards(JSON.parse(localStorage.getItem('dataForGetCards')).emailOfUser, 'jp', page + 1, 1))
                        setPage(page + 1)


                        setPageVisited([...pageVisited, page + 1])


                    }}>
                        .
                    </Button>
                    <button onClick={() => {

                        console.log(' ', pageVisited)
                        console.log('', deleteObject)
                    }}>sdsdsdsd</button>
                </div>
                {console.log(page)}
            </Paper>
            {console.log(rows)}
            {console.log(selected)}

        </Box>
    );
}

