import { REGISTER,ALL_LAUNGAGES,ADDALBUMLOADING, HIDEIMAGEPREVIEWPARAMS, SHOWIMAGEPREVIEWPARAMS,IMAGEPREVIEWPARAMS, CHANGE_LAUNGAGE, GETCARDSJPFORTEST, RESET_TEST_CARD, GET_TEST_CARD, IMITATE_CHANGE_IMAGE, ADD_SEARCH_RESULTS, RESET_CURRENT_PAGE, IMITATE_CREATE_CARD, DP_CURRENT_PAGE_ADD, IMITATE_DELETE_CARD, IMITATE_UPDATE_CARD, IMAGEPREVIEWCHANGE, DP_TOTAL_COUNT, DP_FETCHING_TRUE, DP_FETCHING_FALSE, HIDE_IMAGE_PREVIEW_FOR_CHANGE, SHOW_IMAGE_PREVIEW_FOR_CHANGE, GETCARDSJP, HIDEMESSAGE, SHOWERRORMESSAGE, SHOWBACKDROP, HIDEBACKDROP, SHOWMESSAGE, HIDEERRORMESSAGE, IMAGEPREVIEW, SHOWIMAGEPREVIEW, HIDEIMAGEPREVIEW } from './types'

import axios from "axios"

export function registerAccount(inform) {

    return async dispatch => {
        try {
            dispatch(showBackDrop())
            await axios.post('/api/register', inform, {
                "Content-Type": "application/json"
            }).then((res) => {
                console.log(res.data.message)
                if (!res.data.message) {
                    dispatch({
                        type: REGISTER,
                        payload: res.data
                    })
                    dispatch(hideBackDrop())
                    dispatch(showMessage(' !', "success"))
                    localStorage.setItem("dataForGetCards", JSON.stringify({ emailOfUser: res.data.thisUser._id }))

                    setTimeout(() => {
                        dispatch(hideMessage())
                        window.location.href = "/jp"
                    }, 2000)


                }
                else {
                    dispatch(hideBackDrop())
                    dispatch(showError(res.data.message))
                    setTimeout(() => {
                        dispatch(hideError())
                    }, 3000)
                }


            })
        }
        catch (e) {
            dispatch(hideBackDrop())
            dispatch(showError(e.message))
            setTimeout(() => {
                dispatch(hideError())
            }, 2000)
            return 0
        }
    }




}
export function getCards(user, lng, page, filterTest = 0) {
    return async dispatch => {
        try {
            
            console.log(page)

            console.log(user)
            const getCardsInform = {
                email: user,
                lng
            }

            await axios.post(`/api/allCards?page=${page}`, getCardsInform, {
                "Content-Type": "application/json"
            }).then((res) => {
                if (res.data.message) {
                    dispatch(loadingPostsOff())
                }
                else {
                   
                   if (res.data.items == undefined || res.data.items.length==0){
                   
                    dispatch(loadingPostsOff())
                    dispatch({
                        type:ALL_LAUNGAGES,
                        payload:res?.data?.cardTypes
                    })
                    return
                   }

                    if (filterTest == 0) {
                        if(res?.data?.cardTypes!=null){
                            dispatch({
                                type:ALL_LAUNGAGES,
                                payload:res?.data?.cardTypes
                            })
                        }
                        
                        dispatch({
                            type: GETCARDSJP,
                            payload: res?.data?.items
                        })
                        console.log('RES_DATA_ITEMS', res.data.items)
                        dispatch(DpTotalCount(res.data.totalCount))
                    dispatch(DpCurrentPageAdd())
                    }
                    else {
                        dispatch({
                            type: GETCARDSJPFORTEST,
                            payload: res?.data?.items
                        })
                        console.log('FILTER 1')
                    }

                    
                }

            }).finally(() => {
                if (filterTest == 0) {
                   
                    dispatch(DpFetchingFalse())
                    console.log('final')
                }

               
               
            })

        }
        catch (e) {

            dispatch(showError(e.message))
            return
        }

    }
}
export function createUniqueAlbum(id,params){
return async dispatch=>{
    try{
        dispatch(showBackDrop())
        let data = new FormData()
        data.append('id',id)
        data.append('titleAlbum',params.title)
        data.append('colon',params.colon)
        data.append('image',params.image)
        await axios.post('/api/createUniqueAlbum', data, {
            headers: {
                'content-type': 'multipart/form-data'
            }
        }).then((res)=>{
            if(!res.data.message){
                if (res.data.mesg){
                    dispatch(showMessage(res.data.mesg, 'warning'))
                }
                dispatch({
                    type:ALL_LAUNGAGES,
                    payload:res.data.cardTypes
                })
                
                dispatch(showMessage(res.data.msg, 'success'))
                dispatch(hideBackDrop())
                dispatch(hideImagePreview(true))

            }
            else{
                dispatch(hideBackDrop())
                dispatch(showError(res.data.message))
                setTimeout(() => {
                    dispatch(hideError())
                }, 3000)
            }

        })
    }
    catch(e){
        dispatch(hideBackDrop())
        dispatch(showError(e.message))
        setTimeout(() => {
            dispatch(hideError())
        }, 3000)
    }
}
}

