export function imagePreview(image,menuParams=false) {
    return async dispatch => {
        try {
            let file = image.item(0);


            let reader = new FileReader();
            reader.readAsDataURL(file);
            reader.onloadend = function () {
                if(menuParams==true){
                    dispatch({
                        type: IMAGEPREVIEWPARAMS,
                        payload: reader.result
                    })
                    dispatch({
                        type: SHOWIMAGEPREVIEWPARAMS
    
                    })
                }
                else{
    
                
                dispatch({
                    type: IMAGEPREVIEW,
                    payload: reader.result
                })
                dispatch({
                    type: SHOWIMAGEPREVIEW

                })
                }
            }
            


            
        }
        catch (e) {
            dispatch(showError(e.message))
            setTimeout(() => {
                dispatch(hideError())
            }, 2000)
            return 0
        }

    }
}




