export function createCard(cardInfoJp = '', cardInfoOther = '') {
    return async dispatch => {
        try {
            dispatch(showBackDrop())
            console.log(cardInfoJp)
            let data
            if (cardInfoJp) {
                data = new FormData()
                data.append("kandzi", cardInfoJp.kandzi)
                data.append("read", cardInfoJp.read)
                data.append("translate", cardInfoJp.translate)
                data.append("cardImage", cardInfoJp.image)
                data.append('cardType', cardInfoJp.cardType)
                data.append('userId', JSON.parse(localStorage.getItem('dataForGetCards')).emailOfUser)
            }




            await axios.post('/api/createCard', data, {
                headers: {
                    'content-type': 'multipart/form-data'
                }
            })
                .then(res => {
                    if (!res.data.message) {
                        dispatch(hideBackDrop())

                        dispatch(showMessage(res.data.msg, 'success'))
                        dispatch(imitateCreateCard(res.data.newJpWordListAdd))
                        dispatch(hideImagePreview())
                        setTimeout(() => {
                            dispatch(hideMessage())
                        }, 4000)
                    }
                    else {
                        dispatch(hideBackDrop())
                        dispatch(showError(res.data.message))
                        setTimeout(() => {
                            dispatch(hideError())
                        }, 4000)

                    }




                });
        }
        catch (e) {
            dispatch(showError(e.message))
            setTimeout(() => {
                dispatch(hideError())
            }, 5000)
            return 0
        }
    }
}






