export function updateParamsOfCard(newKandzi, newRead, newTranslate, newImage, idCard, type) {
    return async dispatch => {
        try {

            dispatch(showBackDrop())
            let data = new FormData()

            data.append("kandzi", newKandzi)
            data.append('read', newRead)
            data.append('translate', newTranslate)
            data.append("cardType", type)
            data.append('idOfCard', idCard)
            data.append("userId", JSON.parse(localStorage.getItem('dataForGetCards')).emailOfUser)
            data.append("cardImage", newImage)



            await axios.post('/api/updateParams', data, {
                headers: {
                    'content-type': 'multipart/form-data'
                }
            }).then(res => {
                if (!res.data.message) {
                    dispatch(hideBackDrop())

                    dispatch(showMessage(res.data.msg, 'success'))
                    dispatch(hideImagePreview())
                    dispatch(imitateChangeImage({ image: res.data.image, idCard }))
                    setTimeout(() => {
                        dispatch(hideMessage())
                    }, 4000)
                }
                else {
                    dispatch(hideBackDrop())
                    dispatch(showError(res.data.message))
                    setTimeout(() => {
                        dispatch(hideError())
                    }, 4000)

                }
            })

        }
        catch (e) {
            dispatch(showError(e.message))
            setTimeout(() => {
                dispatch(hideError())
            }, 5000)
            return 0
        }
    }

}









