import React from 'react';
import AppBar from '@mui/material/AppBar';
import { useLocation, useNavigate } from 'react-router-dom'
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';

import Slide from '@mui/material/Slide';
import useScrollTrigger from '@mui/material/useScrollTrigger';
import PropTypes from 'prop-types';
import ExitToAppIcon from '@mui/icons-material/ExitToApp';
import HeaderMenu from './HeaderMenu'



function HideOnScroll(props) {
    const { children, window } = props;
    // Note that you normally won't need to set the window ref as useScrollTrigger
    // will default to window.
    // This is only being set here because the demo is in an iframe.
    const trigger = useScrollTrigger({
      target: window ? window() : undefined,
    });
  
    return (
      <Slide appear={false} direction="down" in={!trigger}>
        {children}
      </Slide>
    );
  }
  
  HideOnScroll.propTypes = {
    children: PropTypes.element.isRequired,
    /**
     * Injected by the documentation to work in an iframe.
     * You won't need it on your project.
     */
    window: PropTypes.func,
  };



export default function Header(props) {
    const location = useLocation();
    const navigate = useNavigate()
    console.log(location.pathname);
    const LoginLogout = () => {
        if (localStorage.getItem('dataForGetCards')!='' && location.pathname!='/login' && location.pathname!='/register') {
            localStorage.setItem('dataForGetCards', '')
            window.location.reload()
        }
        else {
            if (location.pathname == '/register') {
                navigate('/login')
            }
            else if (location.pathname=='/login'){
                navigate('/register')
            }
        }
    }
    return (
        <Box sx={{ flexGrow: 1 }}>
          
            <HideOnScroll {...props}>
            <AppBar style={{
                backgroundColor: 'white'
            }}>
                 <Toolbar>
                 {localStorage.getItem('dataForGetCards') ? <HeaderMenu/> : null}
                    <Typography style={{ color: 'black' }} variant="h6" component="div" sx={{ flexGrow: 1 }}>
                        News
                    </Typography>
                    <Button endIcon={<ExitToAppIcon/>} onClick={LoginLogout} style={{
                        color: 'black'
                    }}>{localStorage.getItem('dataForGetCards') ? '' : location.pathname == '/login' ? '' : ''}</Button>
                </Toolbar>
            </AppBar>
            </HideOnScroll>
        </Box>
    );
}

