import { SHOWMESSAGE, HIDEMESSAGE } from './types'
let state = {
    message: '',
    openMessage: 0,
    typeMessage: '',
   
    
}
export default function showMessageReducer(state = "", action) {
    switch (action.type) {
        case SHOWMESSAGE:
            return { ...state, message: action.payload, openMessage: 1, typeMessage: action.typeOfMessage }
        case HIDEMESSAGE:
            return { ...state, message: '', openMessage: 0, typeMessage: '' }
      
        default: return state
    }
    return state
}

