import { REGISTER,IMAGEPREVIEWPARAMS,ADDALBUMLOADING, HIDEIMAGEPREVIEWPARAMS, SHOWIMAGEPREVIEWPARAMS, GETCARDSJPFORTEST,ALL_LAUNGAGES, IMAGEPREVIEWCHANGE,DP_SCROLL_OFF, ADD_SEARCH_RESULTS, IMITATE_CREATE_CARD, IMITATE_CHANGE_IMAGE, IMITATE_UPDATE_CARD, IMITATE_DELETE_CARD, LOADING_POSTS_OFF, HIDE_IMAGE_PREVIEW_FOR_CHANGE, SHOW_IMAGE_PREVIEW_FOR_CHANGE, GETCARDSJP, IMAGEPREVIEW, SHOWIMAGEPREVIEW, HIDEIMAGEPREVIEW } from './types'
let stateDefault = {
    user: '',
    imagePreview: '',
    showImagePreview: 0,
    imagePreviewForChange: '',
    showImagePreviewForChange: 0,
    imagePreviewParams:'',
    showImagePreviewParams:0,
    loadingPosts: 0,
    searchDpOff:0,
    testPosts:[],
    allLaungages:[],
    albumLoad:false,

    posts: []
}
export default function registerReducer(state = stateDefault, action) {
    switch (action.type) {
        case ADDALBUMLOADING:
            return {...state, albumLoad:action.payload}

        case ALL_LAUNGAGES:
            return {...state, allLaungages:action.payload.reverse()}
        case REGISTER:
            return { ...state, user: action.payload }
        case GETCARDSJP:
            console.log(' ,    ????')
            return { ...state, loadingPosts: 1, posts: [...state.posts, ...action.payload] }
        case GETCARDSJPFORTEST:
            console.log('summ')
            console.log(action.payload)
            return{...state,testPosts:action.payload}
            
        case IMAGEPREVIEW:
            return { ...state, imagePreview: action.payload }
        case SHOWIMAGEPREVIEW:
            return { ...state, showImagePreview: 1 }
        case IMAGEPREVIEWPARAMS:
            return {...state,imagePreviewParams:action.payload}
        case SHOWIMAGEPREVIEWPARAMS:
        return {...state, showImagePreviewParams:1}
        case HIDEIMAGEPREVIEW:
            return { ...state, showImagePreview: 0 }
        case HIDEIMAGEPREVIEWPARAMS:
            return { ...state, showImagePreviewParams: 0 }
        case SHOW_IMAGE_PREVIEW_FOR_CHANGE:
            return { ...state, showImagePreviewForChange: 1 }
        case HIDE_IMAGE_PREVIEW_FOR_CHANGE:
            return { ...state, showImagePreviewForChange: 0 }
        case IMAGEPREVIEWCHANGE:
            return { ...state, imagePreviewForChange: action.payload }
        case LOADING_POSTS_OFF:
            return { ...state, loadingPosts: 1 }
        case IMITATE_DELETE_CARD:
            let itemsFilter = state.posts.filter((e) => {
                return e.idOfCard != action.payload
            })
            return { ...state, posts: itemsFilter }
        case IMITATE_UPDATE_CARD:
            const { newKandzi, idCard, newTranslate, newRead } = action.payload
            const newCard = state.posts.map((item) => (
                item.idOfCard == idCard
                    ? { ...item, kandzi: newKandzi, read: newRead, translate: newTranslate }
                    : item
            ));
            return { ...state, posts: newCard }
        case IMITATE_CHANGE_IMAGE:

            const cardWithNewImage = state.posts.map((item) => (
                item.idOfCard == action.payload.idCard
                    ? { ...item, image: action.payload.image }
                    : item
            ));
            return { ...state, posts: cardWithNewImage }
        case IMITATE_CREATE_CARD:
            console.log('imitatecreate')
            let newJpWordList = [...state.posts];
            let newJpWordListAdd = {
                idOfCard: action.payload.idOfCard, kandzi: action.payload.kandzi, read: action.payload.read, translate: action.payload.translate, image: action.payload.image
            }
            
           
            return { ...state, posts: [newJpWordListAdd, ...state.posts] }
        case ADD_SEARCH_RESULTS:
            console.log('addsearchresult')
            return {...state,posts:action.payload.items,searchDpOff:action.payload.scrollOff}
        case DP_SCROLL_OFF:
            return {...state,searchDpOff:0}
        

        default: return state
    }
    return state
}


