import React from "react"
import { useSelector } from 'react-redux'
import { FlagIcon } from "react-flag-kit";
import { useNavigate } from "react-router-dom";
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';
import DialogTitle from '@mui/material/DialogTitle';
import Dialog from '@mui/material/Dialog';

export default function CreatedAlbums({ onClose, open, openParams }) {
    const navigate = useNavigate()
    const cardTypes = useSelector(state => state.registerInform.allLaungages)
    console.log('CARDTYPEEEEEEEEEEEEEEEEEEEEEES', cardTypes)



    return (
        <Dialog onClose={onClose} open={open}>
            <DialogTitle> :</DialogTitle>
            <List sx={{ pt: 0, width: 400 }}>
                {[...cardTypes].map((lng) => (

                    ['jp', 'us', 'de', 'fr', 'cn'].includes(lng.type) ? <div onClick={() => {
                        navigate(`/${lng.type}`)
                        window.location.reload()
                    }}
                    ><ListItem disableGutters>
                            <ListItemButton key={lng.type}>
                                <ListItemAvatar>
                                    <FlagIcon code={lng.type.toUpperCase()} size={40} />
                                </ListItemAvatar>
                                <ListItemText primary={lng.type} />
                            </ListItemButton> </ListItem></div> : <div
                                onClick={() => {
                                    navigate(`/${lng.type}`)
                                    window.location.reload()
                                }}> <ListItem disableGutters>
                            <ListItemButton key={lng.type}>
                                <ListItemAvatar>
                                    {<img width='45px' src={lng.image} />}
                                </ListItemAvatar>
                                <ListItemText primary={lng.type} />
                            </ListItemButton>
                        </ListItem></div>
                ))}

            </List>
        </Dialog>
    );
}