import {GET_TEST_CARD, CHANGE_LAUNGAGE, RESET_TEST_CARD} from './types'
let stateTest={
    testCard:[{
        translate:'',
        kandzi:'',
        read:'',
        image:'',
        changeLaungage:false,
    }]
}
const testPageReducer=(state=stateTest,action)=>{
    switch(action.type){
        case CHANGE_LAUNGAGE:
            console.log('changeeeeee', action.payload)
            if (action.payload==true){
                console.log('It is true')
            }
            if(action.payload==false){
                console.log('it Is false')
            }
            return {...state, changeLaungage:action.payload}
        case GET_TEST_CARD:
            console.log('AAAACTIONS',action.payload)
            return {...state,testCard:action.payload}
        case RESET_TEST_CARD:
            return{...state,testCard:[{
                translate:'',
                kandzi:'',
                read:'',
                image:''
            }]}
        default: return state
    }
   return state
}

export default testPageReducer;