import React, { useState,useRef} from 'react'
import '../styles/CreateCardForm.module.css'
import ImagePreview from './ImagePreview'
import styless from '../styles/CreateCardForm.module.css'
import { useDispatch, useSelector } from 'react-redux'





const CreateCardForm = ({lng}) => {
  
  const [nameFile, setNameFile] = useState(' ')
  const [createCardInform, setCreateCardInform] = useState({
    kandzi: '',
    read: "",
    translate: '',
    image: "",
    cardType: lng
  })
  const imageUrl2 = useSelector(state => state.registerInform.imagePreview)
  const isShow2 = useSelector(state => state.registerInform.showImagePreview)
  const cardTypes = useSelector(state => {
    const state2 = state.registerInform.allLaungages.filter((cardType)=>{
return cardType.type == lng
    })
    return state2
})
const [cardTypeColon] = cardTypes
 
  
  const dispatch = useDispatch();
 
  const debounceTimer = useRef(null);
  
  function onSearchInput(event) {
    clearTimeout(debounceTimer.current);
    debounceTimer.current = setTimeout(()=>sendSearchQuery(event.target.value, lng), 500);
  }

  function sendSearchQuery(value) {
    import('../redux/actions').then(actions=>{
      dispatch(actions.getSearchResults(value,lng));
    })
      
  }
const cardJpNull=()=>{
  document.getElementById("cardImageJp").value = ""
}
const setCardFormImageReset=()=>{
  setCreateCardInform(prev => ({
    ...prev,
    image: ''
  }))
}
 
  return (
    <div>
      <div id='formkandzi'>
        <div className={styless.kandzisearch}>
          <strong></strong>
        </div>
        <div className={styless.searchinput}>
          <input type="text"
      
      onChange={onSearchInput} className={styless.search} />
        </div>
        {lng == 'jp' ||lng=='cn'  ||cardTypeColon?.colon == 3 ?   <>
        <div className={styless.kandzitext}>

          <strong></strong>
        </div>
        <div className={styless.kandziinput}>
          <input className={styless.kandzi} value={createCardInform.kandzi} type="text" onChange={(e) => {
            setCreateCardInform(prev => ({
              ...prev,
              kandzi: e.target.value
            }))
          }} name="kandzi" />
        </div></> : null}
        <div className={styless.kandzitext}>
          <strong>{lng == 'jp' ||lng=='cn' || cardTypeColon?.colon == 3 ? '' : ''}</strong>
        </div>
        <div className={styless.readinput}>
          <input className={styless.read} type="text" value={createCardInform.read} name="read" onChange={(e) => {
            setCreateCardInform(prev => ({
              ...prev,
              read: e.target.value
            }))

          }} />
        </div>
        <div className={styless.kandzitext}>
          <strong></strong>
        </div>
        <div className={styless.translateinput}>
          <input className={styless.translate} type="text" value={createCardInform.translate} name="translate" onChange={(e) => {
            setCreateCardInform(prev => ({
              ...prev,
              translate: e.target.value
            }))
          }} />
        </div>


      </div>

      <div className={styless.contkandzibutton}>

      {lng == 'jp' ||lng=='cn' || cardTypeColon?.colon == 3 ?<button className={createCardInform.kandzi == '' || createCardInform.read == '' || createCardInform.translate == '' || createCardInform.image == '' ? styless.buttonkandzifalse : styless.buttonkandzi} onClick={e => {
          if (createCardInform.kandzi == '' || createCardInform.read == '' || createCardInform.translate == '' || createCardInform.image == '') {
            let emptyInput = []

            if (createCardInform.kandzi == '') {
              emptyInput.push('  ')
            }
        
            if (createCardInform.read == "") {
              emptyInput.push('  ')
            }
            if (createCardInform.translate == "") {
              emptyInput.push('  ')
            }
            if (createCardInform.image == "") {
              emptyInput.push('   ')
            }
            import ("../redux/actions").then(actions=>{
              dispatch(actions.showMessage(emptyInput.join('; '), 'warning'))
            })
            
            setTimeout(() => {
              import ("../redux/actions").then(actions=>{
                dispatch(actions.hideMessage())
              })
              
            }, 6000)
            return
          }
          import ("../redux/actions").then(actions=>{

            dispatch(actions.createCard(createCardInform))
          })
          document.getElementById("cardImageJp").value = ""
          setCreateCardInform(prev => ({
            ...prev,
            image: ''
          }))
          setCreateCardInform(prev => ({
            ...prev,
            kandzi: ''
          }))
          setCreateCardInform(prev => ({
            ...prev,
            read: ''
          }))
          setCreateCardInform(prev => ({
            ...prev,
            translate: ''
          }))



        }}><strong></strong></button> :<button className={ createCardInform.read == '' || createCardInform.translate == '' || createCardInform.image == '' ? styless.buttonkandzifalse : styless.buttonkandzi} onClick={e => {
          if (createCardInform.read == '' || createCardInform.translate == '' || createCardInform.image == '') {
            let emptyInput = []

          
            if (createCardInform.read == "") {
              emptyInput.push('  ')
            }
            if (createCardInform.translate == "") {
              emptyInput.push('  ')
            }
            if (createCardInform.image == "") {
              emptyInput.push('   ')
            }
            import ("../redux/actions").then(actions=>{
              dispatch(actions.showMessage(emptyInput.join('; '), 'warning'))
            })
            
            setTimeout(() => {
              import ("../redux/actions").then(actions=>{
                dispatch(actions.hideMessage())
              })
              
            }, 6000)
            return
          }
          import ("../redux/actions").then(actions=>{

            dispatch(actions.createCard(createCardInform))
          })
          document.getElementById("cardImageJp").value = ""
          setCreateCardInform(prev => ({
            ...prev,
            image: ''
          }))
          setCreateCardInform(prev => ({
            ...prev,
            kandzi: ''
          }))
          setCreateCardInform(prev => ({
            ...prev,
            read: ''
          }))
          setCreateCardInform(prev => ({
            ...prev,
            translate: ''
          }))



        }}><strong></strong></button>
}

      </div>
      <div className={styless.contadd}>
        <label className={styless.inputfile}>
          <input type="file" name="file" id="cardImageJp" onChange={(e) => {
            setCreateCardInform(prev => ({
              ...prev,
              image: e.target.files[0]
            }))
            setNameFile(e.target.files[0].name)
            import ("../redux/actions").then(actions=>{
              dispatch(actions.imagePreview(e.target.files,false))
            })
            

          }} />
          <span> </span>
        </label>
        <ImagePreview isTrue={false} imageUrl={imageUrl2} isShow={isShow2} cardJpNull={cardJpNull}  setCardFormImageReset={setCardFormImageReset} nameFile={nameFile}/>



      </div>

    </div>
  )
}
export default CreateCardForm