import React, { useEffect, useRef,useState } from "react"
import { useNavigate,useParams } from "react-router-dom";
import { useDispatch, useSelector} from 'react-redux'
import CircularProgress from '@mui/material/CircularProgress';
import { getCards, showError } from '../redux/actions'
import Alert from '@mui/material/Alert';
import AlertTitle from '@mui/material/AlertTitle';
import styless from "../styles/Cardsjp.module.css"
import Card from './Card';
import CreateCardForm from './CreateCardForm';
import { DpFetchingTrue } from '../redux/actions';

import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import TestPage from './TestPage'
import StyleIcon from '@mui/icons-material/Style';
import PsychologyIcon from '@mui/icons-material/Psychology';
import TabPanel from './TabPanel'
  
  function a11yProps(index) {
    return {
      id: `simple-tab-${index}`,
      'aria-controls': `simple-tabpanel-${index}`,
    };
  }

const Cardjp = () => {

  const params=useParams()
  console.log('', params)
  const [isTestPage, setIsTestPage] = useState(false)
    const data = useSelector(state => state)
    const navigate = useNavigate()
    const dispatch = useDispatch()
    const dpStateCurrentPage = useSelector(state => state.dynamicPaginationState.currentPage)
    const cardTypes=useSelector(state => state.registerInform.allLaungages)
    const dpStateFetching = useSelector(state => state.dynamicPaginationState.fetching)
    const dpStateTotalCount = useSelector(state => state.dynamicPaginationState.totalCount)
    const dpScrollOff=useSelector(state=>state.registerInform.searchDpOff)
    const albumLoad=useSelector(state=>state.registerInform.albumLoad)
    console.log(data.registerInform.posts.length)
    console.log(dpStateTotalCount)
    const dpStateTotalCountRef = useRef(dpStateTotalCount);
    const dpStatePosts = useRef(data.registerInform.posts);
    const scrollOff=useRef(dpScrollOff)
    const [value, setValue] =useState(0);
    const isTestPageRef=useRef(isTestPage)
    const registerInform = useSelector(state => state.registerInform);
    const [rows,setRows]=useState(registerInform.testPosts)
    console.log('isTestPageCUrrent',isTestPageRef)
    console.log(rows)
    console.log('totalCount1',dpStateTotalCountRef.current)
    const cardTypesForSaveColon = useSelector(state => {
    
      const state2 = state.registerInform.allLaungages.filter((cardType)=>{
  return cardType.type == params.laungage
      })
      if (!['jp', 'us', 'de', 'fr', 'cn'].includes(params.laungage)){
        localStorage.setItem('colon',state2[0]?.colon)
      }
     
      return state2
  })
  
    useEffect(() => {
      setRows(registerInform.testPosts)
  }, [registerInform.testPosts]);
  
  
  function getNewPage(pageNumber){
    return pageNumber
  }
 


  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
 useEffect(()=>{
isTestPageRef.current=isTestPage
 },[isTestPage])
    useEffect(() => {
        dpStateTotalCountRef.current = dpStateTotalCount;
    }, [dpStateTotalCount]);
    useEffect(() => {
        dpStatePosts.current = data.registerInform.posts;
    }, [data.registerInform.posts]);
    useEffect(() => {
        scrollOff.current = dpScrollOff;
    }, [dpScrollOff]);
    useEffect(() => {
        console.log("useeff")
        console.log(dpStateFetching)
        try {
            console.log(dpStateFetching)
            if (dpStateFetching) {
              
                localStorage.getItem("dataForGetCards") ? dispatch(getCards(JSON.parse(localStorage.getItem("dataForGetCards")).emailOfUser, params.laungage, dpStateCurrentPage))
               : navigate('/login')
            }
            

        }
        catch (e) {
            dispatch(showError(e.message))
            return

        }

    }, [dpStateFetching])
    useEffect(() => {
        document.addEventListener('scroll', scrollHandler)
        return function () {
            document.removeEventListener('scroll', scrollHandler)
        }
    }, [])

useEffect(()=>{
  //   ,            ,      ,   .
  if (cardTypes.length>0){
    if(cardTypes?.some(obj => obj.type === params.laungage || albumLoad==true)){
      console.log('ok')
        }
        else{
          
          import("../redux/actions").then(actions => {
            dispatch(actions.showMessage(' ', 'warning'))
        }).then(()=>{
          navigate('/jp')
          window.location.reload()
        })
          
        }
  }
    
  
  
   
},[cardTypes,albumLoad])

    const scrollHandler = (e) => {
      if (!isTestPageRef.current && dpStatePosts && dpStateTotalCountRef.current) {
     
      if ((e.target.documentElement.scrollHeight - (e.target.documentElement.scrollTop + window.innerHeight) < 100) && (dpStatePosts.current.length < dpStateTotalCountRef.current)&& (scrollOff.current == 0)) {
      dispatch(DpFetchingTrue());
      }
      }
      };

    return (
        <Box sx={{   width: '100%'  }}>
        <Box sx={{ position:'relative',top:1.5, borderBottom: 1, borderColor: 'divider' }}>
          <Tabs variant="fullWidth" value={value} onChange={handleChange}  aria-label="basic tabs example">
            <Tab icon={<StyleIcon />}  onClick={()=>console.log(isTestPageRef.current)} style={{backgroundColor:'white',color:'black'}} label="" {...a11yProps(0)} />
            <Tab icon={<PsychologyIcon/>}  style={{backgroundColor:'white',color:'black'}} label="" {...a11yProps(1)} />
            
          </Tabs>
        </Box>
        <TabPanel value={value} index={0}>
        <div>
            {/* <button onClick={() => {

                console.log(data.registerInform.posts.length)
                console.log(dpStateTotalCount)
                console.log()
                console.log(':', data.registerInform.posts)
            }}>kkkkkkk</button> */}
            <div className={styless.app}>
                <div><CreateCardForm lng={params.laungage}/></div>
                <div style={{
                    color: "red"
                }}>{data.errorMessage ?
                    <Alert severity="error">
                        <AlertTitle>Error</AlertTitle>
                        {data.errorMessage}
                    </Alert>
                    : ""}</div>
 
                {data.registerInform.loadingPosts != 0 ? data.registerInform.posts.length === 0  || data.registerInform.posts==undefined ? <div>   </div> : data.registerInform.posts.map((e) => {
                    return (

                        <div><Card cardType={params.laungage} idCard={e.idOfCard} kandzi={e.kandzi} read={e.read} translate={e.translate} image={e.image} /></div>

                    )

                }) : data.errorMessage ? "" : <CircularProgress style={{overflow:'hidden'}} disableScrollLock={true} color="inherit" />}</div></div>
        </TabPanel>
        <TabPanel value={value} index={1}>
          <TestPage cardType={params.laungage} isTestPage={isTestPage} rows={rows} setIsTestPage={setIsTestPage} totalCount={dpStateTotalCountRef.current}/>
        </TabPanel>
        
      </Box>
       
    )
}
export default Cardjp