import React, { useState, useCallback, useEffect } from 'react';
import { useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from 'react-redux'
import { getCards, showError, hideError } from '../redux/actions';
import Alert from '@mui/material/Alert';
import styless from '../styles/LoginPage.module.css'
import { showMessage, hideMessage, showBackDrop, hideBackDrop } from "../redux/actions"

import axios from "axios";


const LoginPage = () => {
    const navigate = useNavigate()
    const dispatch = useDispatch()
    const posts = useSelector(state => state)
    const [inform, setInform] = useState({
        email: '',
        password: ''
    })
    useEffect(() => {
        if (localStorage.getItem('dataForGetCards')) {
            navigate('/jp')
        }
        else {
            return
        }
    }, [])
    const loginAndSendInformToRedux = useCallback(async () => {
        try {
            dispatch(showBackDrop())
            await axios.post('/api/login', inform, {
                "Content-Type": "application/json"
            }).then((res) => {
                if (!res.data.message) {
                    localStorage.setItem('dataForGetCards',JSON.stringify({emailOfUser:res.data.email}))
                    dispatch(hideBackDrop())
                    dispatch(showMessage(" ", "success"))
                    setTimeout(() => {
                        dispatch(hideMessage())
                    }, 3000)
                    

                    navigate('/jp')

                }
                else {
                    dispatch(hideBackDrop())
                    console.log(res.data.message)
                    dispatch(showError(res.data.message))
                    setTimeout(() => {
                        dispatch(hideError())
                    }, 3000)
                }
            })
        }
        catch (e) {
            dispatch(hideBackDrop())
            dispatch(showError(e.message))
            return
        }

    })


    return (
        <div className={styless.form}>
            <div style={{
                color: "red"
            }}>{posts.errorMessage ? <Alert severity="warning">{posts.errorMessage}</Alert> : ""}</div>
            <div class="mb-3">
                <label for="exampleInputEmail1" class="form-label">Email address</label>
                <input type="email" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" onChange={(e) => setInform(prev => ({
                    ...prev,
                    email: e.target.value
                }))} />

            </div>
            <div class="mb-3">
                <label for="exampleInputPassword1" class="form-label">Password</label>
                <input type="password" class="form-control" id="exampleInputPassword1" onChange={(e) => setInform(prev => ({
                    ...prev,
                    password: e.target.value
                }))} />
            </div>

            <button class="btn btn-light" onClick={loginAndSendInformToRedux}></button>
        </div>
    )
}
export default (LoginPage)
