import React, { useRef, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux'
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import InsertPhotoIcon from '@mui/icons-material/InsertPhoto';
import Dialog from '@mui/material/Dialog';
import Box from '@mui/material/Box'
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import FormControl from '@mui/material/FormControl';
import ImagePreview from './ImagePreview'
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import Select from '@mui/material/Select';


const AddUniqueAlbumMenu = ({ onClose, open }) => {
    const dispatch = useDispatch()
    const fileInputRef = useRef()
    const userInfo=JSON.parse(localStorage.getItem('dataForGetCards')).emailOfUser
    const [nameFile, setNameFile] = useState(' ')
    const [params,setParams]=useState({
        title:'',
        image:'',
        colon:'',

    })
    const imageUrl = useSelector(state => state.registerInform.imagePreviewParams)
    const isShow = useSelector(state => state.registerInform.showImagePreviewParams)
    const cardTypes = useSelector(state => state.registerInform.allLaungages)

    const setTitleUnique =(e)=>{
setParams(prev=>({
   ...prev,
    title:e.target.value
}))
    }
const setColonUnique =(e)=>{
    setParams(prev=>({
       ...prev,
        colon:e.target.value
    }))

    }
    const setImageUnique =(e)=>{
        setParams(prev=>({
           ...prev,
            image:e.target.files[0]
        }))
    }
    const setImageUniqueReset =()=>{
        setParams(prev=>({
           ...prev,
            image:''
        }))
        fileInputRef.current.value=''
    }
    return (

        <Dialog

            open={open}
            onClose={onClose}
        >
            <DialogTitle>  </DialogTitle>
            <DialogContent>
                <DialogContentText>
                               .
                </DialogContentText>
                <Box
                    noValidate
                    component="form"
                    sx={{
                        display: 'flex',
                        flexDirection: 'column',
                        m: 'auto',
                        width: 'fit-content',
                        alignItems: 'center'
                    }}
                >
                    <TextField sx={{
                        width: 260

                    }} onChange={setTitleUnique} id="outlined-basic" label=" " variant="outlined" />
                    <FormControl variant="standard" sx={{ m: 1, minWidth: 120 }}>
                        <InputLabel id="demo-simple-select-standard-label"> </InputLabel>
                        <Select
                            labelId="demo-simple-select-standard-label"
                            id="demo-simple-select-standard"
onChange={setColonUnique}
                            label=""
                        >

                            <MenuItem value={2}></MenuItem>
                            <MenuItem value={3}></MenuItem>

                        </Select>
                    </FormControl>
                    <label >
                        <div id="upload"> <Button startIcon={<InsertPhotoIcon />} style={{ backgroundColor: 'black' }} onClick={() => {
                            fileInputRef.current.click()
                        }} variant="contained" >
                             
                        </Button></div>

                        <input type="file" ref={fileInputRef} style={{
                            // visibility:"hidden",

                            display: "none",



                            // position:"absolute"
                        }} onChange={(e) => {
                            setImageUnique(e)
                            setNameFile(e.target.files[0].name)
                            import("../redux/actions").then(actions => {
                                dispatch(actions.imagePreview(e.target.files, true))
                            })
                        }} />
                    </label>
                    <ImagePreview setCardFormImageReset={setImageUniqueReset} isTrue={true} imageUrl={imageUrl} isShow={isShow} nameFile={nameFile} />

                    <Button style={{ marginTop:10,backgroundColor: 'white',color:'black',border:'1px solid black' }} onClick={() => {
import("../redux/actions").then(actions => {
  
        dispatch(actions.createUniqueAlbum(userInfo,params))
        onClose()
   
    

    
})


                    }} variant="contained" >
                        
                    </Button>
                </Box>
            </DialogContent>

        </Dialog>

    )
}

export default AddUniqueAlbumMenu