import React from 'react'
import {useSelector,useDispatch} from 'react-redux'
import styless from '../styles/CreateCardForm.module.css'

const ImagePreview=({isTrue, imageUrl,isShow,cardJpNull,setCardFormImageReset,nameFile})=>{
    const dispatch=useDispatch()

return(
    <div className={isShow == 1 ? styless.showImagePrev : styless.hideImagePrev}>
          <div className={styless.flexEnd}>
            <div className={styless.closeImagePreview} onClick={() => {
               import ("../redux/actions").then(actions=>{
                dispatch(actions.hideImagePreview(isTrue))
              })
              
              setCardFormImageReset?.()
              cardJpNull?.()
            }}>X</div>
          </div>
          <div className={isTrue==true ? styless.prevwatchTwo : styless.prevwatch}>


            <div className={styless.widthText}> : {nameFile}</div>
            <div><img height="auto" width="150px" src={imageUrl} alt="" /></div>
          </div>
        </div>

)
}
export default ImagePreview