import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import { Provider } from 'react-redux'
import reportWebVitals from './reportWebVitals';
import { BrowserRouter } from 'react-router-dom'
import { configureStore } from "@reduxjs/toolkit"
import registerReducer from './redux/registerReducer'
import showMessageReducer from './redux/showMessageReducer';
import showBackDropReducer from './redux/showBackDropReducer';
import dynamicPaginationReducer from './redux/dynamicPaginationReducer';
import testPageReducer from './redux/testPageReducer'
import errorReducer from './redux/errorReducer';
const store = configureStore({
  reducer: {
registerInform:registerReducer,
errorMessage:errorReducer,
anyMessage:showMessageReducer,
backDrop:showBackDropReducer,
dynamicPaginationState:dynamicPaginationReducer,
testPageState:testPageReducer

  }
})

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  
    <BrowserRouter>
      <Provider store={store}>
        <App />
      </Provider>
    </BrowserRouter>
  
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
