import React, { useState, useEffect } from 'react';
import { useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from 'react-redux'
import Alert from '@mui/material/Alert';
import { registerAccount } from '../redux/actions'
import styless from '../styles/RegisterPage.module.css'


const RegisterPage = () => {
    const navigate = useNavigate()
    const dispatch = useDispatch()
    const posts = useSelector(state => state)


    const [inform, setInform] = useState({
        email: '',
        password: ''
    })
    useEffect(() => {
        if (localStorage.getItem('dataForGetCards')) {

            navigate('/jp')
        }
        else {
            return
        }
    }, [])
    const sendInformToRedux = () => {
        console.log(posts)
        dispatch(registerAccount(inform))


    }


    return (
        <div className={styless.form}>

            <div style={{
                color: "red"
            }}>{posts.errorMessage ? <Alert severity="warning">{posts.errorMessage}</Alert> : ""}</div>
            <div class="mb-3">
                <label for="exampleInputEmail1" class="form-label light">Email address</label>
                <input type="email" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" onChange={(e) => setInform(prev => ({
                    ...prev,
                    email: e.target.value
                }))} />

            </div>
            <div class="mb-3">
                <label for="exampleInputPassword1" class="form-label">Password</label>
                <input type="password" class="form-control" id="exampleInputPassword1" onChange={(e) => setInform(prev => ({
                    ...prev,
                    password: e.target.value
                }))} />
            </div>

            <button class="btn btn-light" onClick={sendInformToRedux}></button>

        </div>
    )
}
export default (RegisterPage)
