import React, { useState } from 'react';

import StyledMenu from './StyledMenu'
import DialogWindow from './ChangeImageDialog'
import styless from '../styles/Menu.module.css';
import MenuItem from '@mui/material/MenuItem';
import EditIcon from '@mui/icons-material/Edit';
import {ImagePreviewForChange} from '../redux/actions';
import {useDispatch} from 'react-redux';
import DeleteIcon from '@mui/icons-material/Delete';
import PhotoLibraryIcon from '@mui/icons-material/PhotoLibrary';
import ChangeInformDialog from './ChangeInformDialog';
import DeleteDialog from './DeleteDialog'

export default function CustomizedMenus({ cardType, newImage, newKandzi, newRead, newTranslate, idCard }) {
    const [anchorEl, setAnchorEl] = React.useState(null);
    const [openDialog, setOpenDialog] = useState(false)
    const [openInformDialog,setOpenInformDialog]=useState(false)
    const [openDeleteDialog,setOpenDeleteDialog]=useState(false)
    const [newImageChange, setNewImageChange] = useState('')
    const dispatch=useDispatch()

    const open = Boolean(anchorEl);
    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };
    const handleClose = () => {
        setAnchorEl(null);
    };
    const closeDialog = () => {
        setOpenDialog(false)
    }
    const closeDeleteDialog=()=>{
        setOpenDeleteDialog(false)
    }
    const handleCloseInform = () => {
        setOpenInformDialog(false)
    };


    return (
        <div>
            <DeleteDialog closeDialog={closeDeleteDialog} openDialog={openDeleteDialog} cardType={cardType} handleCloseMenu={handleClose} idCard={idCard}/>
            <ChangeInformDialog closeDialog={closeDialog} handleCloseMenu={handleClose} handleClose={handleCloseInform} openDialog={openInformDialog} cardType={cardType} newImage={newImage} newKandzi={newKandzi} newRead={newRead} newTranslate={newTranslate} idCard={idCard}/>
            <DialogWindow closeDialog={closeDialog} handleClose={handleClose} openDialog={openDialog} cardType={cardType} newImage={newImageChange} newKandzi={newKandzi} newRead={newRead} newTranslate={newTranslate} idCard={idCard} />
            <div onClick={handleClick} className={styless.menuButtonSize}>...</div>

            <StyledMenu
                id="demo-customized-menu"
                MenuListProps={{
                    'aria-labelledby': 'demo-customized-button',
                }}
                anchorEl={anchorEl}
                open={open}
                onClose={handleClose}


            >
                <MenuItem disableRipple>
                    <PhotoLibraryIcon />




                    <label >
                        <div id="upload" className={styless.labelInput}> </div>
                        <input type="file" style={{
                            // visibility:"hidden",

                            display: "none",



                            // position:"absolute"
                        }} onChange={(e) => {
                            setNewImageChange(e.target.files[0])
                            setOpenDialog(true)
                            dispatch(ImagePreviewForChange(e.target.files))
                        }} />
                    </label>
                </MenuItem>
                <MenuItem onClick={()=>{
                    setOpenInformDialog(true)
                }} disableRipple>
                    <EditIcon />
                    
                </MenuItem>


                <MenuItem onClick={()=>setOpenDeleteDialog(true)} disableRipple>
                    <DeleteIcon />
                    
                </MenuItem>

            </StyledMenu>
        </div>
    );
}