import React, { useState } from 'react'
import MenuItem from '@mui/material/MenuItem';
import StyledMenu from './StyledMenu';
import AddIcon from '@mui/icons-material/Add';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import AddAlbomMenu from './AddAlbomMenu';
import AddUniqueAlbumMenu from './AddUniqueAlbumMenu';
import CreatedAlbums from './CreatedAlbums';
import TypeSpecimenIcon from '@mui/icons-material/TypeSpecimen';
const HeaderMenu = () => {
    const [anchorEl, setAnchorEl] = useState(null);
    const [openMenu, setOpenMenu] = useState(false)
    const [openParamsMenu, setOpenParamsMenu] = useState(false)
    const [openCreatedAlbumsMenu,setOpenCreatedAlbumsMenu]=useState(false)
    const open = Boolean(anchorEl);
    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };
    const handleClose = () => {
        setAnchorEl(null);
    };
    const handleDialogClose = () => {
        setOpenMenu(false)
    }
    const closeCreatedAlbumsMenu=()=>{
        setOpenCreatedAlbumsMenu(false)
    }
    const handleParamsClose = ()=>{
        setOpenParamsMenu(false)
    }
    const openParamsMenuFunc=()=>{
        setOpenParamsMenu(true)
        setOpenMenu(false)
    }

    return (<>
    <CreatedAlbums open={openCreatedAlbumsMenu} onClose={closeCreatedAlbumsMenu}/>
        <AddUniqueAlbumMenu onClose={handleParamsClose} open={openParamsMenu}/>
        <AddAlbomMenu onClose={handleDialogClose} open={openMenu} openParams={openParamsMenuFunc} />
        <IconButton
            size="large"
            edge="start"
            color="inherit"
            aria-label="menu"
            style={{
                color: 'black'

            }}
            sx={{ mr: 2 }}
        >
            <MenuIcon onClick={handleClick} />
        </IconButton>
        <StyledMenu
            id="demo-customized-menu"
            MenuListProps={{
                'aria-labelledby': 'demo-customized-button',
            }}
            anchorEl={anchorEl}
            open={open}
            onClose={handleClose}


        >



            <MenuItem onClick={() => setOpenMenu(true)} disableRipple>
                <AddIcon />
                 
            </MenuItem>
            <MenuItem onClick={() => setOpenCreatedAlbumsMenu(true)} disableRipple>
                <TypeSpecimenIcon/>
                 
            </MenuItem>

        </StyledMenu>


    </>)
}
export default HeaderMenu