import React, { useState, useEffect } from 'react'
import { useNavigate } from 'react-router-dom'
import ErrorCard from './ErrorCard'
import Grid from '@mui/material/Grid';
const ErrorPage = () => {
    const [counter, setCounter] = useState(3)
    const navigate = useNavigate()
    useEffect(() => {
        if (counter === 0) {

            navigate('/cardsJp')
            return
        }
        setInterval(() => {
            setCounter(counter - 1)
        }, 1000)

    }, [counter])
    return (
        <div>
            <br /><br />
            <Grid
                container
                direction="column"
                justifyContent="center"
                alignItems="center"
            ><ErrorCard kandzi='' read='' translate='' image='https://i.pinimg.com/736x/67/0b/c1/670bc1450e8c9d88f5b033495c5f5de9.jpg' /><div style={{fontSize:'30px'}}>    : {counter}</div></Grid></div>
    )
}
export default ErrorPage