import React, { useEffect, useState } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import styless from "../styles/Card.module.css";
import { startTest } from '../redux/actions'
import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos';
const TestStartedPage = ({ selected, offTest, checkAllClick, deleteObject, totalCount,cardType }) => {
    const testItem = useSelector(state => state.testPageState.testCard)
    const changeLaungageOnOrOff=useSelector(state=>state.testPageState.changeLaungage)
    console.log(testItem)
    console.log("CHANGELAUNGAGETEST", changeLaungageOnOrOff)
    const [hover, setHover] = useState(false);
    const [allIndex, setAllIndex] = useState([])
    const [taskNumber, setTaskNumber] = useState(0)
    // const [answer,setAnswer]=useState('')
    // const [getHiraganaAndCatacana,setGetHiraganaAndCatacana]=useState(' /')
    // const [trueAnswer,setTrueAnswer]=useState(true)
    // const [getTrueAnswer,setGetTrueAnswer]=
    const [testProps, setTestProps] = useState({
        fisherYates: [],
        answer: '',
        getHiraganaAndCatacana: ' /',
        trueAnswer: true,

    })
    const cardTypes = useSelector(state => {
        const state2 = state.registerInform.allLaungages.filter((lng)=>{
return lng.type == cardType
        })
        return state2
    })
    const [cardTypeColon] = cardTypes

    const dispatch = useDispatch()
    console.log(' ', testItem)
    useEffect(() => {
        if (checkAllClick == 1) {
            let arrayOfRandomNums = generateArrayAndRandom(totalCount - (deleteObject.length + 1))
            dispatch(startTest(cardType, '', arrayOfRandomNums[0], 1, deleteObject ));
        }
        else {
            dispatch(startTest(cardType,fisherYatesShuffle(selected)[0]));
        }

        setTestProps((prev) => ({
            ...prev,
            fisherYates: fisherYatesShuffle(selected),
        }));
    }, [])
    useEffect(() => {
        if (taskNumber >= selected.length && checkAllClick == 0) {

            setTaskNumber(0);
            setTestProps((prev) => ({
                ...prev,
                fisherYates: fisherYatesShuffle(selected),
            }));

        }
        if (taskNumber >= totalCount - (deleteObject.length + 1)) {
            setTaskNumber(0);
            generateArrayAndRandom(totalCount - (deleteObject.length + 1))
        }

    }, [taskNumber, selected.length]);
    const checkAnswer = () => {
        if (changeLaungageOnOrOff){
            if(cardType=='jp' || cardType=='cn' || cardTypeColon?.colon == 3){
                if (testProps.answer == testItem.kandzi) {
                    setTestProps(prev => ({
                        ...prev,
                        trueAnswer: true
                    }))
                    setTestProps(prev => ({
                        ...prev,
                        answer: ''
                    }))
                    setTestProps(prev => ({
                        ...prev,
                        getHiraganaAndCatacana: ' /'
                    }))
                    setTaskNumber(taskNumber + 1);
                }
                else {
                    setTestProps(prev => ({
                        ...prev,
                        trueAnswer: false
                    }))
                }
            }
            else{
            if (testProps.answer == testItem.read) {
                setTestProps(prev => ({
                    ...prev,
                    trueAnswer: true
                }))
                setTestProps(prev => ({
                    ...prev,
                    answer: ''
                }))
                setTestProps(prev => ({
                    ...prev,
                    getHiraganaAndCatacana: ' /'
                }))
                setTaskNumber(taskNumber + 1);
            }
            else {
                setTestProps(prev => ({
                    ...prev,
                    trueAnswer: false
                }))
            }
        }
        }
        else{
          
        if (testProps.answer == testItem.translate) {
            setTestProps(prev => ({
                ...prev,
                trueAnswer: true
            }))
            setTestProps(prev => ({
                ...prev,
                answer: ''
            }))
            setTestProps(prev => ({
                ...prev,
                getHiraganaAndCatacana: ' /'
            }))
            setTaskNumber(taskNumber + 1);
        }
        else {
            setTestProps(prev => ({
                ...prev,
                trueAnswer: false
            }))
        }
    }
    }

    useEffect(() => {
        console.log('', taskNumber)
        if (selected.length >= 1 && !(taskNumber >= selected.length) && checkAllClick == 0) {
            console.log('OKAY')
            dispatch(startTest(cardType,testProps.fisherYates[taskNumber]));
        }
        if (checkAllClick == 1 && !(taskNumber >= totalCount - deleteObject.length)) {
            console.log('OKAY')
            if (allIndex.length == 0) {

                console.log('.')
            }
            else {
                dispatch(startTest(cardType, '', allIndex[taskNumber], 1, deleteObject))
            }

        }
    }, [taskNumber]);
    // useEffect(()=>{
    //     if (taskNumber>=selected.length){
    //         setTaskNumber(0)
    //         setTestProps(prev=>({
    //             ...prev,
    //             fisherYates:fisherYatesShuffle(selected)
    //         }))

    //     }
    // },[taskNumber])


    function generateArrayAndRandom(num) {
        let A = []
        for (let i = 0; i <= num; i++) {
            A.push(i)
        }
        setAllIndex(fisherYatesShuffle(A))
        return fisherYatesShuffle(A)
    }
    function fisherYatesShuffle(array) {
        for (let i = array.length - 1; i > 0; i--) {
            const j = Math.floor(Math.random() * (i + 1));
            [array[i], array[j]] = [array[j], array[i]];
        }
        return array;
    }
    return (
        <div>
            <ArrowBackIosIcon
                sx={{
                    position: 'relative',
                    top: '-16px',
                    left: '-10px',
                    cursor: 'pointer',

                    transform: hover ? 'scale(1.6)' : 'scale(1.4)',
                    transition: 'transform 0.2s ease-in-out',
                }}
                onMouseEnter={() => setHover(true)}
                onMouseLeave={() => setHover(false)}
                onClick={() => {
                    offTest()
                    setTestProps(prev => ({
                        ...prev,
                        fisherYates: [],
                        answer: '',
                        getHiraganaAndCatacana: ' /',
                        trueAnswer: true,
                    }))
                    import('../redux/actions').then(actions=>{
                       
                        dispatch(actions.changeLaungage(false))
                    })
                }}
            />
            <div style={{
                display: "flex",
                flexDirection: "column",
                justifyContent: "center",
                alignItems: "center",

            }} className={styless.allblocks}>
                <div className={styless.blockTwo}>



                    {/* <button onClick={() => console.log(allIndex)}>dcscscsdscd</button> */}
                    <div id="blockreadandtranslate">

                        <div className={styless.blocktranslateTest}>{changeLaungageOnOrOff ?  testItem.translate : cardType=='jp' || cardType=='cn' ||  cardTypeColon?.colon == 3 ? testItem.kandzi : testItem.read}</div>


                    </div>
                </div>
                <div className={styless.blockphoto}><img width="330px" style={{
                    border: "2px solid black"
                }} height="auto" src={testItem.image} /></div>
                {/* https://i.pinimg.com/736x/67/0b/c1/670bc1450e8c9d88f5b033495c5f5de9.jpg */}
            </div>
            <div style={{
                display: "flex",
                flexDirection: "column",

                justifyContent: "center",
                alignItems: "center",
                gap: "30px",
                marginTop: "20px"
            }}>
                <div id="kandziinput">

                    <input id='kandzi' style={{
                        width: "350px",
                        height: "50px",
                        fontSize: "20px",
                        border: "15px solid white",

                        padding: "5px"
                    }} placeholder="  " value={testProps.answer} type="text" onChange={(e) => {
                        setTestProps(prev => ({
                            ...prev,
                            answer: e.target.value
                        }))


                    }} name="kandzi" /> </div>
                <button className="testButton" style={{
                    width: "350px",
                    height: "50px",
                    fontSize: "16px",
                    backgroundColor: testProps.trueAnswer == false ? 'red' : 'white',
                    border: 'none',
                    padding: "5px"
                }} onClick={checkAnswer}></button>
                <button className="testButton" style={{
                    width: "350px",
                    height: "50px",
                    fontSize: "16px",
                    backgroundColor: "white",
                    border: 'none',
                    padding: "5px",

                }} onClick={() => {
                    setTestProps(prev => ({
                        ...prev,
                        answer: changeLaungageOnOrOff ? cardType=='jp' || cardType=='cn' ||  cardTypeColon?.colon == 3 ? testItem.kandzi : testItem.read : testItem.translate
                    }))
                }}>  </button>
                {cardType == 'jp' ||cardType=='cn' || cardTypeColon?.colon == 3 ?<button className="testButton" style={{
                    width: "350px",
                    height: "50px",
                    fontSize: "16px",
                    backgroundColor: "white",
                    border: 'none',
                    padding: "5px"
                }} onClick={() => {
                    setTestProps(prev => ({
                        ...prev,
                        getHiraganaAndCatacana: testItem.read
                    }))
                }}>{testProps.getHiraganaAndCatacana}</button> : null}
            </div>

        </div>
    )

}
export default TestStartedPage