import React from 'react';

import Dialog from '@mui/material/Dialog';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import Button from '@mui/material/Button';
import { useDispatch, useSelector } from 'react-redux';
import CircularProgress from '@mui/material/CircularProgress';

import { styled } from '@mui/material/styles';


const CustomizedDialog = styled(Dialog)`
  

  & .MuiDialog-paper {
      display:flex;
      justify-content:center;
      align-items:center;
    background-color:black;
    border-radius:0;
    border: 10px dashed rgb(30, 81, 255);
    width:500px;
    
  }
`;
export default function DialogWindow({ handleClose, cardType, newKandzi, newTranslate, newRead, newImage, idCard, openDialog, closeDialog }) {
    const dispatch = useDispatch()
    const imageChangeUrl = useSelector(state => state.registerInform)
    return (
        <CustomizedDialog fullWidth={true}
            maxWidth={'sm'}
            open={openDialog}>
            <div style={{ color: "white", display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center' }}>
                <DialogTitle>  </DialogTitle>
                <div>{imageChangeUrl.showImagePreviewForChange == 1 ? <img style={{ border: '2px dashed white' }} width="150px" src={imageChangeUrl.imagePreviewForChange} alt="" /> : <CircularProgress disableScrollLock={true} color="inherit" />}</div>
            </div>
            <DialogContent>
                <Button style={{ color: "black", border: " 2px solid white", backgroundColor: "white", borderRadius: 0 }} onClick={() => {
                    import("../redux/actions").then(actions => {
                        dispatch(actions.updateParamsOfCard(newKandzi, newRead, newTranslate, newImage, idCard, cardType))
                    })
                    closeDialog()
                    handleClose()
                    import("../redux/actions").then(actions => {
                        dispatch(actions.hideImagePreviewChange())
                    })
                }}> </Button>
            </DialogContent>


        </CustomizedDialog>


    );
}