import React,{useEffect} from 'react';
import {useSelector} from 'react-redux';
import styless from "../styles/Card.module.css";
import Menu from './Menu';





const Card = ({ idCard, translate, read, kandzi, image,cardType }) => {

    const cardTypes = useSelector(state => {
        const state2 = state.registerInform.allLaungages.filter((lng)=>{
            
return lng.type == cardType

        })
        localStorage.setItem('colon',state2[0].colon)
        return state2
    })
    const [cardTypeColon] = cardTypes
    
    console.log('CARDTYPESTWO',cardTypeColon)


    return (
        <div>

            <div className={styless.allblocks}>
                <div className={styless.menu}><Menu cardType={cardType}  newImage={image} newKandzi={kandzi} newRead={read} newTranslate={translate} idCard={idCard} /></div>
                <div className={cardType == 'jp' || cardType=='cn' || cardTypeColon?.colon == 3 ? styless.block: styless.twoBlocks}>

                    {cardType == 'jp' ||cardType=='cn' || cardTypeColon?.colon == 3 ? <div style={{
                        fontSize: "20px",
                        position: "relative",
                        borderRight: "1.5px solid black",
                        width: "100px",
                        height: kandzi.length < 12 ? 80:'auto',
                        //   height: translate.length > 80 ? translate.length + 45 + "px" : translate.length > 35 ? translate.length + 58 + "px" : translate.length > 25 ? translate.length + 71 + "px" : "80px",
                        display: "flex",

                        justifyContent: "center",
                        alignItems: "center",
                        wordBreak: 'break-all'
                    }}>{kandzi}</div> : null}


                    <div className={cardType == 'jp' || cardType=='cn' || cardTypeColon?.colon == 3 ? null: styless.blockreadandtranslate }>
                        <div className={cardType == 'jp' ||cardType=='cn' || cardTypeColon?.colon == 3 ? styless.blockread:styless.blockreadX}>{read}</div>
                        <div className={cardType == 'jp' ||cardType=='cn' || cardTypeColon?.colon == 3 ? styless.blocktranslate:styless.blocktranslateX }>{translate}</div>


                    </div>
                </div>
                <div className={styless.blockphoto}><img width="330px" style={{
                    border: "2px solid black"
                }} height="auto" src={image} /></div>
                {/* https://i.pinimg.com/736x/67/0b/c1/670bc1450e8c9d88f5b033495c5f5de9.jpg */}
            </div>

        </div>
    )
}
export default Card