import React from 'react';
import {useDispatch,useSelector} from 'react-redux'
import Button from '@mui/material/Button';

import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';
import DialogTitle from '@mui/material/DialogTitle';
import Dialog from '@mui/material/Dialog';
import { FlagIcon } from "react-flag-kit";
import AddIcon from '@mui/icons-material/Add';
const laungages=[{
    ISO:'us',
    title:"",
    flag:<FlagIcon code="US" size={48} />
},
{
    ISO:"de",
    title:"",
    flag:<FlagIcon code="DE" size={48} />
},
{
    ISO:'fr',
    title:"",
    flag:<FlagIcon code="FR" size={48} />
},
{
    ISO:'cn',
    title:"",
    flag:<FlagIcon code="CN" size={48} />
}]
export default function AddAlbomMenu({ onClose, open,openParams }) {

  const cardTypes=useSelector(state => state.registerInform.allLaungages)
  console.log('CARDTYPEEEEEEEEEEEEEEEEEEEEEES',cardTypes)
  const dispatch=useDispatch()
  const user=JSON.parse(localStorage.getItem('dataForGetCards')).emailOfUser
 console.log('',[...laungages,...cardTypes])

  return (
    <Dialog onClose={onClose} open={open}>
      <DialogTitle>         </DialogTitle>
      <List sx={{ pt: 0}}>
        {laungages.map((laungage) => (
         cardTypes?.some(obj => obj.type === laungage.ISO) ? null:  <ListItem onClick={()=>{
            import('../redux/actions').then(actions=>{
              dispatch(actions.createAlbum(user,laungage.ISO))
            })
          }} disableGutters>
            <ListItemButton  key={laungage.title}>
              <ListItemAvatar>
              {laungage.flag}
              </ListItemAvatar>
              <ListItemText primary={laungage.title} />
            </ListItemButton>
          </ListItem>
        ))}
<div style={{
  
  paddingTop:'10px',
  display:'flex',
  justifyContent:'center'
}}>
<Button variant="contained" onClick ={openParams}style={{
backgroundColor:"black",
}} startIcon={<AddIcon  />}>  </Button>
</div>
      </List>
    </Dialog>
  );
}
