import {DP_FETCHING_TRUE,RESET_CURRENT_PAGE,DP_FETCHING_FALSE, DP_TOTAL_COUNT, DP_CURRENT_PAGE_ADD} from './types'
let stateDp={
    fetching:true,
    totalCount:0,
    currentPage:1,
}

const dynamicPaginationReducer=(state=stateDp,action)=>{
    switch(action.type){
        case DP_FETCHING_TRUE:
            return {...state, fetching:true}
        case DP_FETCHING_FALSE:
            return {...state,fetching:false}
        case DP_TOTAL_COUNT:
            return {...state, totalCount:action.payload}
        case DP_CURRENT_PAGE_ADD:
            return {...state, currentPage:state.currentPage+1}
        case DP_TOTAL_COUNT:
            return {...state, totalCount:action.payload}
        case RESET_CURRENT_PAGE:
            return {...state,currentPage:action.payload}

        default: return state
    }
    return state
}
export default dynamicPaginationReducer