import Dialog from '@mui/material/Dialog';
import { styled } from '@mui/material/styles';
import styless from '../styles/DeleteDialog.module.css';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import Button from '@mui/material/Button';

import {useDispatch} from 'react-redux'
const CustomizedDialog = styled(Dialog)`
  

  & .MuiDialog-paper {
      display:flex;
      justify-content:center;
      align-items:center;
    background-color:black;
    border-radius:0;
    border: 10px dashed rgb(30, 81, 255);
    width:300px;
    height:180px;
    padding-left:180px;
    padding-right:180px;
    overflow-x:hidden;
    overflow-y:hidden;
  }
`;

const DeleteDialog=({closeDialog, openDialog, cardType, handleCloseMenu, idCard})=>{
    const dispatch=useDispatch()
    return(
        <div>
             <CustomizedDialog fullWidth={true}
            maxWidth={'sm'}
            open={openDialog}>
            <div className={styless.deleteDialogTitle}>
                <DialogTitle>  ?</DialogTitle>
                
            </div>
            <DialogContent>
               <div className={styless.deleteDialogButtons}>
               <Button className={styless.deleteDialogButton} onClick={() => {
                   import ('../redux/actions').then((actions)=>{
                    dispatch(actions.deleteCard(idCard,cardType))
                   })
                  
                   closeDialog()
                }}></Button> <Button className={styless.deleteDialogButton} onClick={() => {
                   closeDialog()
                }}></Button>
               </div>
            </DialogContent>


        </CustomizedDialog>
        </div>
    )
}
export default DeleteDialog